@import "./css/custom.css";

@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --primary-hsl-view: hsl(72, 83%, 24%);
}

@layer base {
  :root {
    --background: 0 0% 100%;
    --foreground: 240 10% 3.9%;
    --card: 0 0% 100%;
    --card-foreground: 240 10% 3.9%;
    --popover: 0 0% 100%;
    --popover-foreground: 240 10% 3.9%;
    --primary: 72 83% 24%;
    --primary-foreground: 355.7 100% 97.3%;
    --secondary: 357 90% 45%;
    --secondary-foreground: 240 5.9% 10%;
    --muted: 240 4.8% 95.9%;
    --muted-foreground: 240 3.8% 46.1%;
    --accent: 240 4.8% 95.9%;
    --accent-foreground: 240 5.9% 10%;
    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 0 0% 98%;
    --border: 240 5.9% 90%;
    --input: 240 5.9% 90%;
    --ring: 206 93% 48%;
    --warning: 357 90% 45%;
    --radius: .7rem;
  }

  .dark {
    --background: 100 3% 20%;
    --foreground: 0 0% 95%;
    --card: 24 9.8% 10%;
    --card-foreground: 0 0% 95%;
    --popover: 0 0% 9%;
    --popover-foreground: 0 0% 95%;
    --primary: 72 83% 24%;
    --primary-foreground: 144.9 80.4% 10%;
    --secondary: 357 90% 45%;
    --secondary-foreground: 0 0% 98%;
    --muted: 0 0% 15%;
    --muted-foreground: 240 5% 64.9%;
    --accent: 12 6.5% 15.1%;
    --accent-foreground: 0 0% 98%;
    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 0 85.7% 97.3%;
    --border: 240 3.7% 15.9%;
    --input: 240 3.7% 15.9%;
    --ring: 206 93% 48%;
  }
}

@layer base {
  * {
    @apply border-border;
  }
}

body {
  font-size: 16px;
}

@media only screen and (max-width: 600px) {
  body {
    font-size: 14px;
  }
}
